<template>
  <div class="bg-dark">
    <header class="row">
      <div id="header-menu" class="col-12 px-4 pt-3 text-start">
        <router-link to="/">
          <i class="bi bi-arrow-left text-white" style="font-size: 2rem"></i>
        </router-link>
      </div>
      <!-- <div id="header-menu" class="col-12 px-4 pt-3 text-start">
        <i class="bi bi-grid-3x3-gap-fill text-white" style="font-size: 2rem"></i>
      </div> -->
      <div id="header-title" class="col text-white text-start p-4">
        <h1>E aí, seja bem-vindo de volta!</h1>
        <router-link to="/register" class="text-white">
        </router-link>
        <p>Acesse sua conta para cadastrar produtos para a sua loja.</p>
      </div>
    </header>
    <main id="results-area" class="row p-4" style="border-top-left-radius: 30px; border-top-right-radius: 30px; background-color: #fefaee">
      <div class="col-12 text-start">
        <div class="mb-3">
          <label for="codeInput" class="form-label">E-mail</label>
          <input type="email" class="form-control p-1" id="emailInput" placeholder="Ex. ondetem@gmail.com" v-model="email">
        </div>
        <div class="mb-3">
          <label for="passwordInput" class="form-label">Senha</label>
          <input type="password" class="form-control p-1" id="passwordInput" placeholder="" v-model="password">
        </div>
        <button type="submit" class="btn btn-dark p-2 form-control d-flex justify-content-evenly row" @click="submitCredentials">
          <div class="col-2">
            <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true" v-if="loading"></span>
          </div>
          <div class="col-8 text-middle">
          Acessar minha conta
          </div>
          <div class="col-2">
          </div>
        </button>
      </div>
    </main>
  </div>
</template>

<style scoped>
</style>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import api from '../services/ondetem-api.js'

export default {
  name: 'Home',
  components: {
    // HelloWorld
  },
  data() {
    return {
      loading: false,
      email: '',
      password: '',
      token: '',
      loginFailed: false
    }
  },
  methods: {
    async submitCredentials() {
      let response = []
      
      try {
        this.loading = true

        if (this.email != '' && this.password != '' && this.secretWord != '') {
          response = await api.post("credentials/validate", {
            "email": this.email,
            "password": this.password
          })

          if (response.status == 201) {
            this.token = response.data.token
            localStorage.token = this.token

            this.$router.push({ path: '/company/dashboard' })
          } else {
            this.loginFailed = true
          }
        }

        this.loading = false
      } catch(err) {
        console.log("Login failed because " + err)
      }
    }
  },
}
</script>
